import React from 'react';

import './Contacto.css';

const Contacto = () => {
    return (

<section id="contacto">
      <div className="container-fluid">
        <div className="container">
          <div data-wow-delay=".6s" className="wow fadeInDown col-lg-12 text-center title">
            <h2 className="text-center">Contacto</h2>
            <div className="section-title text-center">
              <div className="separator"></div>
            </div>
            <br></br>
            <br></br>
          </div>
          <div>
            <form>              
              <div className="form-group row">
                <div className="form-group col-xs-6 col-md-6 text-left">
                  Nombre *
                  <div className="form-group your-name">
                      <input className="form-control" name="your-name" type="text" aria-required="true" required />
                  </div>
                </div>
                <div className="form-group col-md-6 text-left">
                  Tu correo electrónico *
                  <div className="form-group your-email">
                      <input className="form-control" name="your-email" type="email" aria-required="true" required />                        
                  </div>
                </div>

                <div className="form-group col-md-12 text-left">
                  Mensaje *
                  <div className="form-group your-message">
                      <textarea className="form-control" name="your-message" rows="4" aria-required="true" required ></textarea>
                  </div>
                </div>
              </div>

              <div className="form-group col-md-6 offset-md-3">
                <input type="submit" name="enviar" value="Enviar mensaje" className="btn btn-dark btn-md btn-block" />
              </div>
            </form>
          </div>
        </div>
      </div>
    </section>

    );
}

export default Contacto;
