import React from 'react';

import './Parallax.css';

import Background from '../imagenes/parallax.jpg';

const myStyles = {
    backgroundImage: `url( ${Background} )`,
    // height: '50vh',
    // backgroundSize: 'cover'
}

const Parallax = () => {
    return (

    <section id="parallax" style={myStyles}>

    <div className="container-fluid">
      <p className="text-center">La información obtenidas por el equipo de ventas y marketing</p>
      <h2 className="text-center">Estas son nuestras cifras</h2>
      <div className="section-title text-center">
        <div className="separator"></div>
      </div>
      <br></br>
      <br></br>
      <div className="row">
        <div className="col-4">
          <div className="list-progress__item left">
            <i className="fa fa-address-book-o fa- "></i>
            <div className="list-progress__inner">
              <p><strong>600+</strong>
              <br></br>
                Ventas Realizadas</p>
            </div>
          </div>
        </div>
        <div className="col-4">
          <div className="list-progress__item center">
            <i className="fa fa-archive fa- "></i>
            <div className="list-progress__inner">
              <p><strong>200+</strong><br></br>
                Solicitudes en concesionario</p>
            </div>
          </div>
        </div>
        <div className="col-4">
          <div className="list-progress__item right">
            <i className="fa fa-chrome fa- "></i>
            <div className="list-progress__inner">
              <p><strong>15+</strong><br></br>
                Concesionarias a nivel nacional</p>
            </div>
          </div>
        </div>
      </div>
    </div>
  </section>

              
  );
}

export default Parallax;