import React from 'react';
// import {Link} from 'react-router-dom';
import './Navegacion.css';
import logo from '../logo.png';

const Navegacion = () => {
    return (
        <nav className="navbar navbar-expand-md  fixed-top" id="mainNav2">
        <div className="container">
          <a href="www.google.com" className="img">
            <img src={logo} alt="logo" />
          </a>
          <button className="navbar-toggler navbar-toggler-right" type="button" data-toggle="collapse" data-target="#navbar" aria-expanded="false">
            <i className="fa fa-bars" aria-hidden="true"></i>
          </button>
      
          <div className="navbar-collapse">
            <div id="navbar" className="collapse navbar-collapse">
              <ul id="menu-menu-principal" className="nav navbar-nav navbar-right navbar-collapse">
                <li><a href="#principal" className="nav-link js-scroll-trigger">Inicio</a></li>
                <li><a href="#caracteristicas" className="nav-link js-scroll-trigger">Caracteristicas</a></li>
                <li><a href="#galeria" className="nav-link js-scroll-trigger">Galeria</a></li>
                <li><a href="#comentarios" className="nav-link js-scroll-trigger">Comentarios</a></li>
                <li><a href="#contacto" className="nav-link js-scroll-trigger">Contacto</a></li>
              </ul>
            </div>
          </div>
          
        </div>
      </nav>
    );
}

export default Navegacion;



